import { Component, OnInit, ViewEncapsulation, OnDestroy } from '@angular/core';
import * as $ from 'jquery';
import { SocketService }  from '../services/ws-socket.service';
import { Subscription } from 'rxjs/Subscription';
import { RGB } from '../models/rgbCommands';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None,
  providers: [SocketService]
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'app';
  private connection: Subscription;

  constructor(private socketService: SocketService) {}

  ngOnInit(): void {
    this.registerHandler();
    this.registerSocketService();
  }

  ngOnDestroy(): void {
    this.connection.unsubscribe();
  }

  registerSocketService () {
    this.connection = this.socketService.connect().subscribe(message => {
      console.log(message);
    })
  }

  registerHandler(){
    const that = this;
    $(".terminal-screen #editSpan").keyup(function (e) {
      if (e.keyCode == 13) {
        that.enterKeyHandle();
      }
    });
  }

  enterKeyHandle() {
    var editLineCommand = this.returnUserInput();
    var detachedEditInput = this.cloneEditInput();
    this.addRemoveEditClass(detachedEditInput);
    this.handleUserCommands(editLineCommand);
    $(".terminal-screen p:empty").remove();
    $(".terminal-screen p:last-child").after(detachedEditInput);
  }

  handleUserCommands(editLineCommand) {
    var loweredCommand = editLineCommand.toLocaleLowerCase();
    var lineClr = "light-green";
    if (loweredCommand === "cls" || loweredCommand === "clear") {
      this.clearScreenCommand();
    } else {
      let typedColor: string = RGB[loweredCommand];
      if(typeof typedColor === "undefined"){
        this.pushCommand(loweredCommand);
      } else {
        editLineCommand = `${editLineCommand} did you mean green?`;
        lineClr = undefined
      }
      this.repeatCommand(editLineCommand, lineClr);
    }
  }

  returnUserInput(): string {
    // Get edit command
    var editLineText = $("#editSpan").val();
    // Reset edit command
    $("#editSpan").val("");
  
    return editLineText.toString();
  }

  cloneEditInput() {
    // Detach editLine span preserve event listeners
    var detachedInput = $("#editLine").detach();
    return detachedInput;
  }

  addRemoveEditClass(detachedEditInput) {
    // Remove class for css reasons
    detachedEditInput.removeClass("edit-line");
    detachedEditInput.addClass("edited-line");
  }

  clearScreenCommand() {
    $("p:not(:first-child)").empty();
  }

  repeatCommand(editLineCommand, lineClr = "light-red") {
    var inputCommand = `<p class='cmd-line ${lineClr}'>$ ${editLineCommand}<p>`;
    $(".terminal-screen p:last-child").after(inputCommand);
  } 
  
  pushCommand(colourCommand : string){
    this.socketService.pushRGBCommand(colourCommand);
  }
}


