import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import * as io from 'socket.io-client';

@Injectable()
export class SocketService {
  private url = 'https://rgb-socket-api.herokuapp.com/';
  private socket;

  constructor() { }

  connect(): Observable<{}> {
   this.socket = io(this.url);
    let observable = Observable.create(stream =>
      this.socket.on('rgb-pull', (data) => { stream.next(data)})
    )
    return observable;
  }
  pushRGBCommand(rgbCommand: string) {
    this.socket.emit('rgb-push', rgbCommand);
  }
}



